<template>
    <div class="BOX-ForgotPassword" style="height: 100vh;display: flex;justify-content: center;align-items: center;">
        <v-card class="bxForgotPassword">
            <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
                style="margin:auto;"
			>
				<p style="margin-bottom:0px;text-align:center;width: 100%;">{{NotifText}}</p>
			</v-snackbar>
            <v-card-title class="headline">
                Forgot Password
                </v-card-title>
            <v-card-text>
            <div class="bx__forgotpassword">
                <v-form ref="formforgotpassword" v-model="valid" lazy-validation>
                    <p style="font-size:0.9em;text-align:center;margin-bottom: auto;">We just need your registered email address to send you password reset.</p>
                    <br>
                    <!-- <v-text-field
                        v-model="emailConfirm"
                        :rules="emailRules"
                        label="Your E-mail"
                        required
                        class="initest"
                    ></v-text-field> -->
                    <v-text-field
                        v-model="newPassword"
                        :rules="[rules.required, rules.min]"
                        :type="show1 ? 'text' : 'password'"
                        :append-icon="show1 ? 'fa-eye' : 'fa-eye-slash'"
                            @click:append="show1 = !show1"
                        name="input-10-1"
                        label="New Password"
                        hint="At least 8 characters"
                    ></v-text-field>
                    <v-text-field
                        v-model="confirmPassword"
                        :rules="[rules.passMatch, rules.required, rules.min]"
                        :type="show2 ? 'text' : 'password'"
                        :append-icon="show2 ? 'fa-eye' : 'fa-eye-slash'"
                            @click:append="show2 = !show2"
                        name="input-10-1"
                        label="Confirm New Password"
                        hint="At least 8 characters"
                    ></v-text-field>
                    <br>
                    <v-card-actions style="justify-content: center;">
                        <v-btn color="primary" @click="ForgotPassword" :disabled="!valid" class="text-capitalize">
                        Reset Password
                        </v-btn>
                    </v-card-actions>
                </v-form>
            </div>
            </v-card-text>
        </v-card>
        <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
    </div>
</template>

<script>
    export default{
        data () {
            return {
                show1: false,
                show2: false,
                valid: false,
                emailConfirm: '',
                emailRules: [
					v => !!v || 'E-mail is required',
					v => /.+@.+/.test(v) || 'E-mail must be valid'
                ],
                newPassword: '',
                confirmPassword: '',
                rules: {
					required: value => !!value || 'Required.',
					min: v => v.length >= 8 || 'Min 8 characters',
                    passMatch: (v) => v == this.newPassword || 'New Password and Confirm Password must match'
                },
                dialogLoding: false,
                snackbar: false,
                timeout: 5000,
                NotifColor:'',
                NotifText:'',
            }
        },
        created() {
            // console.log(this.$route.params)

        },
        mounted() {
            if (this.$route.params.fp === undefined){
                this.$router.push("/");
            }
        },
        computed: {
            ResetStatus(){
                return this.$store.state.ResetPassword
            }
        },
        methods:{
            ForgotPassword (){
                if (this.$refs.formforgotpassword.validate()) {
                    this.dialogLoding = true
                    const ResetPass ={
                        token: this.$route.params.fp,
                        new_password: this.newPassword,
                        confirm_new_password: this.confirmPassword,
                    }
                    this.$store.dispatch('RESET_PASSWORD',ResetPass)

                }
            }
        },
        watch: {
            ResetStatus(val){
                if (!val) return

                if(val.message === 'success'){
                    setTimeout(() => {
                        this.dialogLoding = false
                        this.NotifColor ='success'
                        this.NotifText="Update password success"
                        this.snackbar = true
                        this.$refs.formforgotpassword.reset()
                    }, 2000);

                    setTimeout(() => {
                        this.$router.push("/")
                    }, 8000);
                }else{
                    this.dialogLoding = false
                    this.NotifColor ='warning'
                    this.NotifText = "Please try again later"
                    this.snackbar = true
                    this.$refs.formforgotpassword.reset()
                }
                
            }
        },
    }
</script>

<style lang="scss">
    @import './ForgotPassword.scss';
</style>


